import React, { Component } from 'react';
import "bootstrap/dist/css/bootstrap.css";
import BillItem from "./BillItem";
import SearchedContent from './SearchedContent';

class Dashboard extends Component {
    
    state = { searchedOrds: "",
     myBills: [{billId: 1, itemsSpecification: "cake", dateOfOrder: "31-01-2021", orderedBy: "Arjun", deliveryDate: "01-02-2021", supervisedBy: "Swiggy", quantity: 1, rate: 550, gst: 50, paidBy: "Arjun", paidAmount: 600, paidRemarks: "Paid by Card", selectedUnit: "Lumpsum", selectedPaymentMode: "Debit Card", orderImg: "https://images.unsplash.com/photo-1578985545062-69928b1d9587?ixlib=rb-1.2.1&ixid=MXwxMjA3fDB8MHxwaG90by1wYWdlfHx8fGVufDB8fHw%3D&auto=format&fit=crop&w=980&q=80"},
     {billId: 2, itemsSpecification: "chocolates", dateOfOrder: "30-01-2021", orderedBy: "Chinna", deliveryDate: "31-02-2021", supervisedBy: "Zomato", quantity: 2, rate: 420, gst: 40, paidBy: "Chinna", paidAmount: 460, paidRemarks: "Paid by Card", selectedUnit: "Lumpsum", selectedPaymentMode: "Debit Card", orderImg: "https://i.insider.com/5f342122505f5320161cf686?width=1136&format=jpeg"}] };

    deleteOrder = bId => {
        var otherOrds = this.state.myBills.filter(bills => bills.billId !== bId);
        this.setState({myBills: otherOrds});
    }

    search = args => {
        this.setState({searchedOrds: args.target.value});
    }

    editOrder = (oldBId, newBId, ordBy, ordItem, ordDate, ordImg) => {
        
        var otherOrds = this.state.myBills.filter(bills => bills.billId !== oldBId);
        this.setState({myBills: [{billId: newBId, orderedBy: ordBy, orderedItem: ordItem, orderDate: ordDate, orderImg: ordImg}, ...otherOrds]});
    }

    reset = () => this.setState({searchedOrds: ""});    
    
    render() { 
        return ( 
                <div className = "table-responsive">
                    <h3 style = {{textAlign: "center"}}>Order Details</h3>
                    <div>
                    <label htmlFor="searchOrd">Search </label>
                    <input type = "text" className = "form-control" name = "searchOrd" onChange = {this.search}/>
                    <span> </span>
                    <input type = "button" className = "btn btn-warning" value="Reset" onClick = {this.reset}/>
                    </div>
                <table className = "table table-bordered">
                        <tbody>
                            <tr>
                                <th style = {{textAlign: "center"}}>Bill ID</th>
                                <th style = {{textAlign: "center"}}>Ordered Items</th>
                                <th style = {{textAlign: "center"}}>Date of Order</th>
                                <th style = {{textAlign: "center"}}>Ordered By</th>
                                <th style = {{textAlign: "center"}}>Delivery Date</th>
                                <th style = {{textAlign: "center"}}>Supervised By</th>
                                <th style = {{textAlign: "center"}}>Quantity</th>
                                <th style = {{textAlign: "center"}}>Rate</th>
                                <th style = {{textAlign: "center"}}>GST</th>
                                <th style = {{textAlign: "center"}}>Paid By</th>
                                <th style = {{textAlign: "center"}}>Paid Amount</th>
                                <th style = {{textAlign: "center"}}>Payment Remarks</th>
                                <th style = {{textAlign: "center"}}>Selected Unit</th>
                                <th style = {{textAlign: "center"}}>Selected Payment Mode</th>
                            </tr>
                        </tbody>
                        <tbody>
                            {this.state.myBills.map(bill => 
                            {
                                if(this.state.searchedOrds !== ""){
                                    if(bill.orderedBy.indexOf(this.state.searchedOrds) === -1){
                                        console.log("No match found");
                                    } else {
                                        return(<BillItem key = {bill.billId} {...bill} deleteOrder = {this.deleteOrder} editOrder = {this.editOrder}/>);
                                    }
                                } else {
                                    return (
                                        <BillItem key = {bill.billId} {...bill} deleteOrder = {this.deleteOrder} editOrder = {this.editOrder}/>
                                        );
                                }
                                return null;
                            }
                            )}
                            
                        </tbody>
                </table>
                <br/>
                <SearchedContent textSearched = {this.state.searchedOrds}></SearchedContent>
                </div>
            
                 );
    }
}
 
export default Dashboard;