import React from 'react';
import ReactDOM from 'react-dom';
import './index.css';
import Header from "./components/Header";
import Dashboard from "./components/Dashboard";
import Footer from "./components/Footer";
import UserInput from "./components/AddBill";
import {BrowserRouter} from "react-router-dom";
import Root from './components/Root';
import AddBill from './components/AddBill';
import Demo from './components/Demo';
import Demo1 from './components/Demo1';

import { Provider } from 'react-redux';
import { applyMiddleware, createStore } from 'redux';
import createSagaMiddleware from 'redux-saga';
import reducer from './store/reducer';
import Keep_Watching_While_Change_Getting_Called from './sagas/saga';

//Install saga using :  npm i redux-saga

const myLogger = (store)=>
                      {
                            return (nextFunctionToExecute)=>
                            {
                              // below if code to execute them dispatch happens
                              return (action)=>
                              {
                                console.log("Component is trying to dispatch some action which is " + action);

                                // below code means we are calling next middleware function in the pipeline .
                                // whichever is planned or if not planned then -- shoot a call to reducer
                                nextFunctionToExecute(action); //pass on control to reducer
                              }
                            }
                      };

const someSagaMiddleware = createSagaMiddleware();
const store = createStore(reducer, applyMiddleware(myLogger, someSagaMiddleware));

someSagaMiddleware.run(Keep_Watching_While_Change_Getting_Called);

