
//debugger;
const initialEmpData = {no: 0, name: "", address: ""};

function reducer(state = initialEmpData, action)
{
    //debugger;
    if(action.type == "CHANGE")
    {

        //What is async op comes in picture??
        //How do we use async call to server / ajax call in react??
        return {...state, no: 100, name: "upgrad", address: "chennai"};
    }

    if(action.type == "CHANGEASYNC")
    {
        //Here we will be getting data from saga and we will be using it.//
        return {...state, no: action.myData.no, name: action.myData.name, address: action.myData.address};
    }
    return state;
}

export default reducer;