import {Component} from 'react';
import { connect } from 'react-redux';

class Demo1 extends Component {
    constructor(props){
        super(props);
        console.log("Testing Component Created");
        console.log("props values are " + this.props);
    };
    
    render() { 
        console.log("rendering...............");
        return (<div>
            <h1>This is Testing Component</h1>
            No: <span>{this.props.myemp.no}</span><br/>    
            Name: <span>{this.props.myemp.name}</span><br/>
            Address: <span>{this.props.myemp.address}</span><br/>
            <button onClick={this.props.DoChange}>Click Me</button>
        </div>  );
    }
}



let mapStateToProps = (state)=>{return  {myemp: state}};
let mapEventHandlersToProps = (dispatch)=>{ return {DoChange: ()=>{ dispatch({type: "CHANGE"})} }};
export default connect(mapStateToProps, mapEventHandlersToProps)(Demo1);