import "bootstrap/dist/css/bootstrap.css";
import { useHistory } from "react-router-dom";
import { useState } from "react";
import AuthHelper from "./AuthHelper";

function Login () {

    const history = useHistory();
    const [email, setEmail] = useState("");
    const [password, setPassword] = useState("");

    let inputHandler = args => {
        console.log(args.target.value);
        if(args.target.id === "email"){
            if(args.target.value === "")
                console.log("Email field empty!!");
            else setEmail(args.target.value);
        } else if(args.target.id === "password"){
            if(args.target.value === "")
                console.log("Password field empty!!");
            else setPassword(args.target.value);
        }
    }

    let login = (args) =>{
        args.preventDefault();
        AuthHelper.login(email, password, (authCheck) => {
                if(authCheck){
                    history.push("dashboard")
                }else{
                    history.push("login");
                }
        });
    }


        return <div className="container login-container">
        <div className="row">
            <div className="col-md-6 login-form-1">
                <h3>Login for Form 1</h3>
                <form onSubmit={login}>
                    <div className="form-group">
                        <input type="text" id="email" className="form-control" placeholder="Your Email *" onChange={inputHandler}/>
                    </div>
                    <div className="form-group">
                        <input type="password" id="password" className="form-control" placeholder="Your Password *" onChange={inputHandler}/>
                    </div>
                    <div className="form-group">
                        <input type="submit" className="btnSubmit" value="Login" />
                    </div>
                    <div className="form-group">
                        <a href="#" className="ForgetPwd">Forget Password?</a>
                    </div>
                </form>
            </div>
        </div>
    </div>; 
}
 
export default Login;