import "bootstrap/dist/css/bootstrap.css";
import {Redirect, Route} from "react-router-dom";
import AuthHelper from "./AuthHelper";

function RouteWrapper( {component: Component, ...restOfTheParameters}, props) 
    { 
                            //path="/secure" component={Secure} or 
                            //path="/secure" component={Login}  
                            
         return  (<Route {...restOfTheParameters}
                    render={
                            (props)=>
                                { 
                                    if(AuthHelper.isAuthenticated())
                                    {
                                        return <Component {...props}/> 
                                        // return <props.component {...props}/>
                                    }
                                    else
                                    {
                                        // return <Login></Login> //<--why not this b'coz url should change to /login 
                                        return <Redirect to={{ pathname: "/login", state:{from:props.location} }}/>
                                    }
                                }}         
                   /> 
                  )
         
    }

export default RouteWrapper;