import React, { Component } from 'react';

class AddBill extends Component {

    state = 
    {  
        fname: "",
        fnameError: "",
        age: "",
        ageError: ""
    }

    onChange = args => {
        if(args.target.id === "fname")
        {   
            this.setState({fname: args.target.value});
        }
        if(args.target.id === "age"){
            this.setState({age: args.target.value});
        }
        debugger;
    }
    
    onFormSubmit = (args) => {
        args.preventDefault();
        
        if(this.state.fname === "")
        {
            this.setState({fnameError: "Name is compulsory!"});

        }
        if(this.state.age === "")
        {
            this.setState({ageError: "Age is compulsory!"});
            //Do ajax call over here!!
        }
    }

    render() { 
        return ( 
            <form id="myForm" action="someCodeOnServer.php" method="post">
                    <legend>Add New Bill Here</legend>
                    <div className="form-group">
                        <label htmlFor="fname">Name</label>
                        <input type="text" className="form-control" 
                               id="fname" name="fname" 
                            //    value={this.state.fname}
                               onChange={this.onChange}
                               placeholder="Enter Your Name"/>
                        <label className="info-danger">{this.state.fnameError}</label>
                    </div>
                    <div className="form-group">
                        <label htmlFor="age">Age</label>
                        <input type="text" className="form-control" 
                               id="age" name="age"
                            //    value = {this.state.age}
                               onChange={this.onChange}
                               placeholder="Enter Your Age"/>
                        <label className="info-danger">{this.state.ageError}</label>
                    </div>
                    <button type="submit" onClick={this.onFormSubmit} className="btn btn-primary">Add Bill</button>
            </form>
         );
    }
}
 
export default AddBill;