import React, { Component } from 'react';
import Header from "./Header";
import Dashboard from "./Dashboard";
import Footer from "./Footer";
import AddBill from "./AddBill";
import Home from "./Home";
import ContactUs from "./ContactUs";
import {Route, Switch, Link} from "react-router-dom";
import Management from './Management';
import Login from './Login';
import RouteWrapper from "./RouteWrapper";


class Root extends Component {

    render() { 
        return ( 
            <div id = "myContainer">
                <Header></Header>
                <hr/>
                <Link to = "/">Home</Link> | <Link to = "/login">Login</Link> | <Link to = "/dashboard">Dashboard</Link> | <Link to = "/contactus">Contact Us</Link> | <Link to = "/addbill">Add Bill</Link> | <Link to = "/management">About Us</Link>
                <hr/>             
                <Switch>                  
                    <Route exact path = "/management"><Management /></Route>

                    <RouteWrapper exact path="/dashboard" component={Dashboard}/>
                    <RouteWrapper exact path="/addbill" component={AddBill}/>

                    <Route exact path = "/contactus"><ContactUs /></Route>
                    <Route exact path = "/login"><Login /></Route>
                    <Route exact path = "/"><Home /></Route>
                    <Route path = "*" component = {() => "Page requested is not found"}/>
                </Switch>
                <hr/>
                <Footer></Footer>
            </div>
         );
    }
}
 
export default Root;