import React, { Component } from 'react';

class BillItem extends Component {
    state = {  }

    editItem = () => {
        var bId = prompt("Order ID: ");
        var ordBy = prompt("Ordered By: ");
        var ordItem = prompt("Ordered Item: ");
        var ordDate = prompt("Ordered Date: ");
        var ordImg = prompt("Ordered Image URL: ")
        this.props.editOrder(this.props.billId, bId, ordBy, ordItem, ordDate, ordImg);
    }

    render() { 
        return ( 
            <tr>
                <td style = {{textAlign: "center"}}>{this.props.billId}</td>
                <td style = {{textAlign: "center"}}>{this.props.itemsSpecification}</td>
                <td style = {{textAlign: "center"}}>{this.props.dateOfOrder}</td>
                <td style = {{textAlign: "center"}}>{this.props.orderedBy}</td>
                <td style = {{textAlign: "center"}}>{this.props.deliveryDate}</td>
                <td style = {{textAlign: "center"}}>{this.props.supervisedBy}</td>
                <td style = {{textAlign: "center"}}>{this.props.quantity}</td>
                <td style = {{textAlign: "center"}}>{this.props.rate}</td>
                <td style = {{textAlign: "center"}}>{this.props.gst}</td>
                <td style = {{textAlign: "center"}}>{this.props.paidBy}</td>
                <td style = {{textAlign: "center"}}>{this.props.paidAmount}</td>
                <td style = {{textAlign: "center"}}>{this.props.paidRemarks}</td>
                <td style = {{textAlign: "center"}}>{this.props.selectedUnit}</td>
                <td style = {{textAlign: "center"}}>{this.props.selectedPaymentMode}</td>
                <td style = {{textAlign: "center"}}><img className = "img-thumbnail" src = {this.props.orderImg} alt = "orderImage"
                 style = {{height: 200 + "px", borderRadius: 4 + "px", padding: 5 + "px", width: 300 + "px"}}/></td>
                <td style = {{textAlign: "center"}}><input type="button" value = "Delete" className = "btn btn-danger"
                 onClick = {() => this.props.deleteOrder(this.props.billId)}/>
                <br/><br/><br/><br/><input type="button" value = "Edit" className = "btn btn-warning"
                 onClick = {() => this.editItem()}/></td>
            </tr>
             );
    }
}

 
export default BillItem;