 import { useEffect, useReducer, useState } from "react";
 import MyReducer from '../store/MyReducer';
 
function Demo()
{
   const [age, dispatch] = useReducer(MyReducer, 0);
   //reducer is a function which will change the values of age when condition is true
   //it also provides some initial value given as a second parameter here in useReducer hook
   //means age will have default value as 0!

   return (<div>
       <h1> Age is {age}</h1>
        <button onClick={()=>dispatch("i")}>Increase Age</button><br/>
        <button onClick={()=>dispatch("d")}>Decrease Age</button><br/>
   </div>)
};

export default Demo;
