import {put, takeEvery, takeLatest} from 'redux-saga/effects';

function* CHANGEASYNC()
{
        // do server call
        
        const jsonData =  yield fetch('https://reqres.in/api/users/1')
                                    .then(response=>response.json());
        console.log(jsonData);
        // get the reply    
        //now that u have data .. now do forward your change action

        yield put({type: 'CHANGEASYNC', myData: {no: jsonData.data.id, name:jsonData.data.first_name, address:jsonData.data.last_name}})
}

function* Keep_Watching_While_Change_Getting_Called()
{
        //we need to write some code here which will be called
        //when action.type =" change" gets called 

        //hold that request
        
        //takeLatest means if there is already call placed and if user 
        //tries to shoot a call to change again by clicking 
        //it will raise many calls in pipeline
        //to avoid that takeLatest will consider last call being attached 
        //to pipeline call
        yield takeLatest('CHANGE', CHANGEASYNC);
}

export default Keep_Watching_While_Change_Getting_Called;